import React, { useCallback, useEffect, useState } from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay, FreeMode, Pagination } from 'swiper/modules'
import { useTranslation } from 'react-i18next'
import { useMediaQuery } from 'react-responsive'
import 'swiper/css'
import 'swiper/css/free-mode'
import 'swiper/css/pagination'
import 'swiper/css/autoplay'
import Coin from './Coin'
import style from './Coins.module.css'
import Loading from '../Events/Loading'
import Error from '../Events/Error'
import CoinsSelector from './CoinsSelector'
import CoinCard from '../Reusable/CoinCard'
import TableHeader from '../Reusable/TableHeader'

const Coins = (props) => {
  const { toggleSelectedCoinId } = props

  // 
  const { t } = useTranslation()

  //
  const isDesktop = useMediaQuery({ minWidth: 1551 })
  const isLaptop = useMediaQuery({ minWidth: 1101, maxWidth: 1550 })
  const isTablet = useMediaQuery({ minWidth: 511, maxWidth: 1100 })
  const isMobile = useMediaQuery({ maxWidth: 510 })

  //  coinSelector
  const [currentStart, setCurrentStart] = useState(100)
  const [prevStart, setPrevStart] = useState(0)
  const [fetchStart, setFetchStart] = useState(0)

  //   
  const [error, setError] = useState('')
  const [isLoading, setIsLoading] = useState(true)
  const [isRefreshing, setIsRefreshing] = useState(false)

  //  tableHeader
  const [sortDirection, setSortDirection] = useState(false) // true -  
  const [sortField, setSortField] = useState('market_cap_usd')

  //API   
  const COINS_API_URL_FEATURED =
    'https://api.coinlore.net/api/tickers/?start=0&limit=6'
  const NOT_AVAILABLE = 'N/A'
  const [coins, setCoins] = useState([])
  const [featuredCoins, setFeaturedCoins] = useState([])

  const fetchResource = async (url) => {
    const res = await fetch(url)
    if (!res.ok) throw new Error(`${url} failed to fetch`)
    const data = await res.json()
    if (!data) throw new Error(`No data found for ${url}`)
    return data
  }

  const fetchData = useCallback(async () => {
    const COINS_API_URL = `https://api.coinlore.net/api/tickers/?start=${fetchStart}&limit=100`
    try {
      const dataCoins = await fetchResource(COINS_API_URL)
      setCoins(dataCoins.data)
      const dataFeaturedCoins = await fetchResource(COINS_API_URL_FEATURED)
      setFeaturedCoins(dataFeaturedCoins.data)
    } catch (error) {
      setError(error.message)
    } finally {
      setIsLoading(false)
      setIsRefreshing(false)
    }
  }, [fetchStart])

  useEffect(() => {
    fetchData() //  

    const intervalId = setInterval(() => {
      fetchData() //   
    }, 10000) //   10 

    return () => clearInterval(intervalId) //     
  }, [fetchData])

  //coins selector  
  const handleStart = (direction) => {
    setFetchStart((prev) => {
      const step = 100
      const multiplier = direction ? 1 : -1

      const newStart = prev + step * multiplier
      setCurrentStart((prevCurrent) => prevCurrent + step * multiplier)
      setPrevStart((prevPrev) => prevPrev + step * multiplier)
      return newStart
    })
  }

  // 
  // const handleStartRefresh = () => {
  //   fetchData(fetchStart)
  // }

  //  
  const sortedCoins = [...coins].sort((a, b) => {
    //    
    const valueA =
      a[sortField] !== undefined && a[sortField] !== null
        ? a[sortField]
        : NOT_AVAILABLE
    const valueB =
      b[sortField] !== undefined && b[sortField] !== null
        ? b[sortField]
        : NOT_AVAILABLE

    //  
    const isNumberA = !isNaN(parseFloat(valueA)) && isFinite(valueA)
    const isNumberB = !isNaN(parseFloat(valueB)) && isFinite(valueB)
    //  
    if (isNumberA && isNumberB) {
      return sortDirection
        ? parseFloat(valueA) - parseFloat(valueB)
        : parseFloat(valueB) - parseFloat(valueA)
      //   1  
    } else {
      return sortDirection
        ? valueA.localeCompare(valueB)
        : valueB.localeCompare(valueA)
    }
  })

  const handleSort = (field) => {
    if (sortField === field) {
      setSortDirection((prev) => !prev)
    } else {
      setSortField(field)
      setSortDirection(true)
    }
  }

  //  tableHeader
  let tableColumns = 8

  if (isDesktop) {
    tableColumns = 8
  } else if (isLaptop) {
    tableColumns = 7
  }

  //  coinCard

  let coinCardsQuantity = 5

  if (isDesktop) {
    coinCardsQuantity = 5
  } else if (isLaptop) {
    coinCardsQuantity = 4
  } else if (isTablet) {
    coinCardsQuantity = 9
  }

  //       coind card

  if (error) {
    return <Error error={error} />
  }

  if (isLoading) {
    return <Loading type={t('coins.loading')} />
  }

  return (
    <div className={style.coinsContainer}>
      <div className={style.coinsFeaturedContainer}>
        <h1 className={style.coinsFeaturedHeader}>{t('coins.header')}</h1>
        <div className={style.coinsFeaturedBox}>
          {isTablet ? (
            <Swiper
              slidesPerView={1}
              spaceBetween={30}
              freeMode={true}
              pagination={{
                clickable: true,
              }}
              modules={[FreeMode, Pagination, Autoplay]}
              autoplay={{
                delay: 3000, // 3 
                disableOnInteraction: false, //   
                pauseOnMouseEnter: true, //   
              }}
              className={style.coinsFeaturedSwiper}
            >
              {featuredCoins.slice(0, coinCardsQuantity).map((featuredCoin) => (
                <SwiperSlide className={style.coinsFeaturedSlide}>
                  <CoinCard
                    key={featuredCoin.id}
                    {...featuredCoin}
                    toggleSelectedCoinId={toggleSelectedCoinId}
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          ) : (
            featuredCoins
              .slice(0, coinCardsQuantity)
              .map((featuredCoin) => (
                <CoinCard
                  key={featuredCoin.id}
                  {...featuredCoin}
                  toggleSelectedCoinId={toggleSelectedCoinId}
                />
              ))
          )}
        </div>
      </div>
      <div className={style.coinsMidLine}></div>
      <CoinsSelector
        toggleStart={handleStart}
        toggleIsRefresh={setIsRefreshing}
        currentStart={currentStart}
        prevStart={prevStart}
      />
      <div className={style.coinsMidLine}></div>
      <div className={style.coinsTable}>
        <TableHeader
          tableArray={[
            { label: t('coins.tableIcon'), field: 'name', image: false },
            { label: t('coins.tableName'), field: 'name', image: true },
            { label: t('coins.tablePrice'), field: 'price_usd', image: true },
            {
              label: t('coins.tableChange'),
              field: 'percent_change_24h',
              image: true,
            },
            { label: t('coins.tableVolume'), field: 'volume24', image: true },
            { label: t('coins.tableSupply'), field: 'csupply', image: true },
            {
              label: t('coins.tableCap'),
              field: 'market_cap_usd',
              image: true,
            },
            ...(isDesktop
              ? [{ label: t('coins.tableAction'), field: 'name', image: false }]
              : []),
          ]}
          tableColumns={tableColumns}
          toggleSort={handleSort}
          sortField={sortField}
          sortDirection={sortDirection}
        />
      </div>
      {isRefreshing ? (
        <Loading type={`Top ${prevStart + 1} - ${currentStart} coins `} />
      ) : (
        <div className={style.coinTableContainer}>
          {sortedCoins.map((coin) => (
            <Coin
              key={coin.id}
              {...coin}
              toggleSelectedCoinId={toggleSelectedCoinId}
            />
          ))}
        </div>
      )}
      <div className={style.coinsMidLine}></div>
      <CoinsSelector
        toggleStart={handleStart}
        toggleIsRefresh={setIsRefreshing}
        currentStart={currentStart}
        prevStart={prevStart}
      />
      <div className={style.coinsMidLine}></div>
    </div>
  )
}

export default Coins
